.QandA{
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    display: flex;
    flex-direction: column;
    color: white;
    align-items: center;
}
.FAQsQuestion{
    background-color: #303030;
    display: flex;
    justify-content: space-between;
    margin-top: 10px;
    align-items: center;
    width: 60vw;
    padding: 0px 40px;
    height: 70px;
    cursor: pointer;
    text-align: justify;
}
.FAQs h2{
    font-weight: 500;
    padding: 20px;
    font-size: 1.6rem;
}
.FAQsAddIcon{
    padding: 30px;
    font-size: 2rem;
    transition: 350ms;
}
.FAQsCancelIcon{
    -webkit-transform: rotateZ(45deg);
            transform: rotateZ(45deg);
}
.FAQsAnswer{
    background-color: #303030;
    width: 60vw;
    margin-top: 3px;
    display: grid;
    align-items: center;
    padding: 0px 40px;
    text-align: justify;
}
body{
    background: black;
    padding: 0;
    margin: 0;
    overflow-x: hidden;
}
.homePage-Header{
    position: absolute;
    z-index: 10;
    display: flex;
    width: 100%;
    justify-content: space-between;
    align-items: center;
    margin-top: 30px;
}
.homePage-Header-Logo{
    width: 28vmin;
    padding-top: 0;
    margin-left: 50px;
}
.homePage-Header-Signin{
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    font-weight: 600;
    border-radius: 4px;
    padding-top: .5rem;
    padding-left: 1rem;
    padding-right: 1rem;
    padding-bottom: .5rem;
    font-size: 3vmin;
    color: white;
    margin-left: 40px;
    background-color: #e50914;
    border-width: 0ch;
    cursor: pointer;
    margin-right: 50px;
}
a:link{
    text-decoration: none;
    color: white;
} 
a:visited{
    color: white;
}    
.homePage-banner{
    width: 100%;
    position: relative;
    top: 0%;
    color: white;
    height: 100vh;
}
.homePage-bannerCover{
    position: absolute;
    background: linear-gradient(to top,rgba(0, 0, 0, 0.733),rgba(0, 0, 0, 0.438),rgba(0, 0, 0, 0.815)), url(/clone-app/static/media/Banner.b32933a6.jpg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    height: 100%;
    width: 100%;
    display: flex;
    bottom: 0;
    top: 0;
    justify-content: center;
    text-align: center;
}
.homePage-bannerContent{
    position: relative;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    top: 20%;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}
.homePage-bannerContent h1{
    font-size: 3.125rem;
    font-weight: 700;
    margin: 20px 0;
}
.homePage-bannerContent h2{
    font-size: 1.625rem;
    margin-top: .3rem;
    font-weight: 600;
    margin: 20px 0;
}
.homePage-bannerContent h3{
    font-weight: 600;
    font-size: 1.2rem;
    margin-bottom: 1.5rem;
    margin-top: 0;
    margin: 20px 0;
}
.homePage-bannerBox{
    display: flex;
    justify-content: center;
}
.homePage-bannerButton{
    background-color: #dc0510;
    border: none;
    color: white;
    font-size: 1.625rem;
    padding: .7rem 2rem;
    border-radius: 2px;
    font-weight: 500;
    white-space: nowrap;
}
.homePage-bannerEmail{
    max-width: 100%;
    min-width: 70%;
    padding-left: 10px;
    font-size: 1rem;
    font-weight: 500;
}
.bannerButtonArrow{
    margin-left: 15px;
    font-weight: 600;
}
@media (max-width: 720px) {
    .homePage-bannerBox{
        flex-direction: column;
        width: 70%;
        margin: auto;
    }
    .homePage-bannerEmail{
        padding: 20px;
    }
}
.homePage-card{
    background-color: black;
    display: flex;
    vertical-align: middle;
    color: white;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}
hr{
    margin: 0;
    width: 100%;
    height: 10px;
    background-color: #222222;
    border: none;
}
.homePage-cardTextbox{
    display: inline-block;
    padding: 60px;
    max-width: 500px;
    margin: auto;
}
.homePage-cardTextbox h1{
    font-size: 3.2rem;
    margin-bottom: .5rem;
}
.homePage-cardTextbox h2{
    line-height: 1.4;
    font-weight: 600;
    font-size: 1.5rem;
    padding: 0;
    margin-top: .5rem;
}
.homePage-cardImagebox{
    display: inline-block;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin: auto;
}
.homePage-cardTV{
    position: relative;
}
.homePage-cardScreen{
    position: absolute;
}
.homePage-cardTVScreenVideo{
    position: absolute;
    top: 100px;
    left: 80px;
}
.homePage-cardScreenVideo{
    position: absolute;
    top: 40px;
    left: 120px;
    width: 400px;
}
.FAQs{
    padding: 40px 0;
}
.footer{
    color: #757575;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0;
    padding: 0;
    margin-top: 60px;
}
.language_footer{
    font-family: 'Roboto', sans-serif;
    font-size: 1rem;
    color: #757575;
    background-color: transparent;
