}
.footcontainer h3{
    font-weight: 600;
    font-size: 1rem;
    margin: 20px 0;
    font-family: 'Roboto', sans-serif;
}
.footcontainer table{
    width: 100%;
    font-size: .8rem;
    margin-bottom: 1.5rem;
}
.footcontainer td{
    padding-top: .8rem;
    font-family: 'Roboto', sans-serif;
}
.footcontainer p{
    padding-top: 1.5rem;
    padding-bottom: 1.5rem;
    font-size: smaller;
