}
input:focus{
    outline: none;
}
button:hover{
    cursor: pointer;
}
.homeBanner{
    top: 0;
    background-position: center center;
    background-size: cover;
}
.homeBannerImg{
    max-height: 100vh;
    visibility: hidden;
}
.homeBannerTextbox{
    position: absolute;
    max-width: 600px;
    top: 200px;
    left: 30px;
}
.homeBannerTextbox h1{
    font-size: 3.5rem;
}
.homeBannerTextbox p{
    margin: 20px 0;
    font-size: 1.4rem;
    font-style: normal;
    line-height: 1.2;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    text-overflow: ellipsis;
    overflow: hidden;
    -webkit-box-orient: vertical;
}
.homeBannerPlay{
    border: 0;
    color: black;
    background-color: rgba(255, 255, 255, 0.795);
    font-size: 1rem;
    font-weight: bold;
    height: 42px;
    margin-right: 20px;
    border-radius: 4px;
    cursor: pointer;
    padding: 0px 30px;
    transition: 350ms;
}
.homeBannerInfo{
    border: 0;
    color: white;
    background-color: #6a6363c2;
    font-size: 1rem;
    font-weight: bold;
    height: 42px;
    margin-right: 20px;
    border-radius: 4px;
    cursor: pointer;
    padding: 0px 30px; 
    transition: 350ms;
}
.homeBannerPlay:hover{
    color: white;
    background-color: rgba(32, 32, 32, 0.829);
}
.homeBannerInfo:hover{
    color: white;
    background-color: rgba(32, 32, 32, 0.863);
}
*{
    font-family: 'Roboto', sans-serif;
}
body{
    background-color: #111111;
    color: white;
    overflow-x: hidden;
    margin: 0;
    padding: 0;
}
.homeTopbar{
    position: absolute;
    width: calc(100vw - 60px);
    display: flex;
    justify-content: space-between;
    height: 50px;
    align-items: center;
    padding: 20px 30px;
    z-index: 999;
    top: 0;
}
.homeTopbarLeft{
    display: flex;
}
.homeLogo{
    width: 136px;
    height: 42px;
    margin-right: 10px;
}
.homeTopbarLeft ul{
    list-style: none;
    display: flex;
    align-self: center;
    font-weight: bold;
    font-size: 17px;
}
.homeTopbarLeft li{
    margin: 0 10px;
    cursor: pointer;
}
.homeTopbarRight{
    display: flex;
    align-items: center;
}
.homeSearchIcon{
    display: flex;
    align-self: center;
    margin-right: 25px;
    font-size: 20px;
    cursor: pointer;
}
.homeTopbarProfilePic{
    width: 35px;
    border-radius: 2px;
    height: 35px;
}
.homeTopbarMenuBtn{
    cursor: pointer;
    color: white;
    background-color: transparent;
    border: none;
    margin: 0 20px;
    font-size: larger;
}
.homeMenu{
    position: absolute;
    top: 80px;
    right: 30px;
    background-color: #1b1b1b;
    min-width: 180px;
    display: flex;
    flex-direction: column;
}
.homeMenu ul{
    list-style: none;
    font: .85em sans-serif;
    font-weight: 600;
    padding: 0;
}
.homeMenuAvatarList{
    border-bottom: 1px solid rgb(230, 229, 229);
}
.homeMenu li{
    display: flex;
    align-items: center;
    padding: 7px 20px;
}
.homeMenu li:hover{
    cursor: pointer;
    background-color: rgb(0, 0, 0);
}
.homeMenuAvatar{
    width: 30px;
    margin-right: 20px;
    height: 30px;
}
.homeMenuSettings li{
    padding-top: 10px;
    padding-bottom: 10px;
}
a:link{
    text-decoration: none;
    color: white;
} 
a:visited{
    color: white;
}    
.homeContentRow{
    margin: 0px 0 40px 30px;
}
.homeContentRow h4{
    margin: 0 0 15px 0;
}
.homeContentPosters{
    display: flex;
    overflow-x: scroll;
    overflow-y: hidden;
    -ms-overflow-style: none;
    scrollbar-width: none;
    align-items: center;
}
.homeContentThumbnail{
    height: 130px;
    border-radius: 6px;
    margin-right: 6px;
    transition: 350ms;
    cursor: pointer;
}
.homeContentThumbnail:hover{
    height: 170px;
}
.homeContentVerticalThumbnail{
    height: 250px;
    border-radius: 6px;
    margin-right: 6px;
    transition: 350ms;
    cursor: pointer;
}
.homeContent{
    position: relative;
    margin-top: -80px;
}
.playMovieContainer{
    display: flex;
    justify-content: center;
    flex-direction: column;
    padding-top: 20px;
    padding: 20px 300px;
}
.playMovieContainer h1{
    text-align: left;
}
.playMovieContainer p{
    text-align: justify;
}
.closeIcon{
    font-size: xx-large;
    text-align: right;
    padding: 10px 0;
    cursor: pointer;
}
body{
    padding: 0;
    margin: 0;
    color: white;
}
.login{
    width: 100%;
    overflow-x: hidden;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    background-image: linear-gradient(to top,rgba(0, 0, 0, 0.562),rgba(0, 0, 0, 0.301),rgba(0, 0, 0, 0.815)), url(/clone-app/static/media/Banner.b32933a6.jpg);
}
.signInbutton{
    display: inline-block;
    width: 100%;
    height: 100%;
}
a:link{
    text-decoration: none;
    color: white;
} 
a:visited{
    color: white;
}    
.login-content{
    padding-bottom: 40px;
}
.loginShade{
    width: 100%;
    display: flex;
    text-align: center;
    align-items: center;
    flex-direction: column;
}
.loginHeaderLogo{
    width: 160px;
    padding-top: 30px;
    padding-left: 30px;
}
.loginPopup{
    background-color: rgba(0, 0, 0, 0.712);
    display: flex;
    flex-direction: column;
    width: 320px;
    padding: 20px 60px;
    text-align: left;
    top: 7rem;
    border-radius: 5px;
}
.loginPopup h2{
    font-size: 2rem;
    padding-bottom: 2rem;
    padding-top: 0;
    padding-left: 0;
    font-weight: 610;
}
.loginPopup .loginInputbox{
    margin-bottom: 1.5rem;
    width: 100%;
    border: none;
    background-color: #333333;
    border-radius: 3px;
    font-size: 1rem;
    padding-top: 1rem;
    padding-bottom: 1rem;
    padding-left: 18px;
    color: #8c8c8c;
}
.loginPopup button{
    cursor: pointer;
    margin-bottom: 1.5rem;
    border: none;
    background-color: #e50914;
    border-radius: 3px;
    font-size: 1rem;
    padding-top: 1rem;
    padding-bottom: 1rem;
    width: 338px;
    color: white;
    font-weight: 600;
}
.loginPopup button:disabled{
    background-color: #ec1923;
}
.loginSavebox{
    display: flex;
    width: 338px;
    color: #8c8c8c;
    font-size: .9rem;
    align-items: last baseline;
}
.loginSavebox input{
    width: 15px;
    margin-right: 4px;
    margin-bottom: 80px;
}
.loginPopup p{
    color: #8c8c8c;
    font-size: .9rem;
    margin-bottom: 1rem;
    padding: 0;
    margin-top: 0;
}
.loginPopup h4{
    font-weight: 500;
    margin-bottom: 1rem;
}
.loginFooter{
    background-color: rgba(0, 0, 0, 0.788);
    width: 100%;
    padding: 30px;
    margin: 0;
}
.loginContainer{
    width: 70%;
    margin-left: 15%;
    align-items: center;
    text-align: left;
    color: #8c8c8c;
}
.loginContainer h4{
    margin-top: 2rem;
    margin-bottom: 2rem;
    font-weight: 500;
    padding: 0;
}
.loginContainer table{
    width: 100%;
    font-size: .9rem;
    padding: 0;
    margin: 0;
}
.loginContainer td{
    margin-right: .9rem;
    padding-bottom: .9rem;
    padding-top: 0;
}
.loginContainer select{
    border: #8c8c8c solid 1px;
    background-color: black;
    color: #8c8c8c;
    padding: .8rem;
    margin-top: 2rem;
    margin-bottom: 4rem;
}
.loginWrongPass{
    color: red;
    text-align: center;
    margin-bottom: 10px;
}
button:focus{
    outline: none;
}
